import React, {Component, Suspense} from "react";
import {AvFeedback, AvForm, AvGroup, AvInput} from "availity-reactstrap-validation";
import {Button, Container, FormGroup, Label} from "reactstrap";
import Loader from "../../../components/Loader";
import firebase from "firebase/app";
import "firebase/auth";
import "firebase/database"
const Topbar = React.lazy(() => import("../../../components/Topbar"));
const Navbar = React.lazy(() => import("../../../components/Navbar"));
const loading = () => <Loader/>;


class choixCreationSociete extends Component{


    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            isMenuOpened: false,
            activeMenuItem: 'item-gestion',
            email:'',
            password:'',
        };
    }

    changeActivePage = (page) => event => {
        if (page !== '') {
            this.setState({
                activePage: page
            })
        }
    };




    render() {
        return(

            <div className="app center-menu ">

                <header id="topnav">
                    <Suspense fallback={loading()}>
                        <Topbar changeActivePage={this.changeActivePage}/>
                    </Suspense>
                </header>

                <div className="wrapper">
                    <Container fluid>
                        <Suspense fallback={loading()}>
                            <div className="card" style={{marginTop: 25}}>
                                <div className="card-body ">

                                    <div className="row" style={{marginTop: 15}}>
                                        <div className="col-lg-12">

                                            <AvForm onValidSubmit={(event, values) => {

                                                this.setState({loading:true})

                                                firebase.auth().signInWithEmailAndPassword(values.email, values.pwd).then( res => {

                                                    console.log("OLD USER")
                                                    localStorage.setItem('uid',res.user.uid);
                                                    localStorage.setItem('email',res.user.email);
                                                    localStorage.setItem('username',res.user.displayName)
                                                    this.setState({loading:false})

                                                        this.props.history.push('/marketplace');

                                                }).catch(err => {
                                                    console.log("NEW USER");
                                                    firebase.auth().createUserWithEmailAndPassword(values.email, values.pwd).then( r => {

                                                        localStorage.setItem('uid',r.user.uid);
                                                        localStorage.setItem('email',r.user.email);
                                                        this.setState({loading:false})

                                                            this.props.history.push('/marketplace');

                                                    }).catch(err2 => {
                                                        this.setState({loading:false})
                                                    })
                                                    /*console.log(err);
                                                    if(err.code === "auth/invalid-email"){
                                                        this.openSnackbar('error', "Adresse mail incorrect !");
                                                        this.setState({loading:false})
                                                    }
                                                    if(err.code === "auth/wrong-password"){
                                                        this.openSnackbar('error', "Mot de passe incorrect !");
                                                        this.setState({loading:false})
                                                    }
                                                    if(err.code === "auth/user-not-found"){
                                                        this.openSnackbar('error', "Utilisateur non trouvé !");
                                                        this.setState({loading:false})
                                                    }*/
                                                });


                                            }}>
                                                <div className="row">
                                                    <div className="col-lg-3"/>
                                                    <div className="col-lg-6">
                                                        <h4 className="header-title mt-0 mb-3">
                                                            Entrez votre adresse mail et mot de passe ?
                                                        </h4>
                                                    </div>
                                                    <div className="col-lg-3"/>
                                                </div>

                                                <div className="row">
                                                    <div className="col-lg-3"/>
                                                    <div className="col-lg-3">
                                                        <AvGroup className="mb-3">
                                                            <AvInput type="text" name="email" id="email" placeholder="Email" required/>
                                                            <AvFeedback>Adresse mail invalide</AvFeedback>
                                                        </AvGroup>
                                                    </div>
                                                    <div className="col-lg-3">
                                                        <AvGroup className="mb-3">
                                                            <AvInput type="password" name="pwd" id="pwd" placeholder="Mot de passe" required/>
                                                            <AvFeedback>Veuillez entrer votre mot de passe</AvFeedback>
                                                        </AvGroup>
                                                    </div>
                                                    <div className="col-lg-3"/>
                                                </div>





                                                <FormGroup>
                                                    <div className="text-center">
                                                        <Button color="primary" className="btn btn-danger waves-effect waves-light">
                                                            {
                                                                this.state.loading ?
                                                                    <div
                                                                        className="spinner-border avatar-xs text-white m-1"
                                                                        role="status"></div> : 'Connecter'
                                                            }
                                                        </Button>
                                                    </div>
                                                </FormGroup>
                                            </AvForm>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Suspense>
                    </Container>
                </div>



            </div>
        )
    }


}

export default choixCreationSociete;