import React, {Component, Suspense} from "react";
import Loader from "../../../components/Loader";
import {Button, Container} from "reactstrap";
import firebase from "firebase/app";
import "firebase/database"
import augmCapitalService from "../../../provider/augmCapitalService";
import entrepriseSARLService from "../../../provider/entrepriseSARLService";
import pdfImage from "../../../assets/images/pdfimage.jpg";
import SignatureCanvas from "react-signature-canvas";
import MySnackbarContentWrapper from "../../../tools/customSnackBar";
import Snackbar from "@material-ui/core/Snackbar";
import signatureService from "../../../provider/signatureService";


const Topbar = React.lazy(() => import("../../../components/Topbar"));
const Navbar = React.lazy(() => import("../../../components/Navbar"));
const loading = () => <Loader/>;

class signDocsSARLFromInvitation extends Component {

    constructor(props) {
        super(props);
        this.state = {
            openAlert: false,
            alertMessage: '',
            alertType: '',
            loading: false,

            showGerantDocs: false,
            showAssocDocs: false,

            codeSMSTaped: '',
            codeSMSsended: '',

            gernats:[],
            member:''

        };
    }

    sigCanvas = {};

    componentWillMount() {
        const {uid, type, index} = this.props.match.params;
        if (type === "gerant") this.setState({showGerantDocs: true});
        else this.setState({showAssocDocs: true});

            firebase.database().ref('/society/' + uid).on('value', (snapshot) => {
                let society = snapshot.val() || [];
                let gerants = society.sAdministrator || [];
                let associes = society.sAssociate || [];
                let member = {};
                if(type === "gerant") {
                    member = gerants[index];
                    this.setState({member:gerants[index],gerants:gerants})
                }
                else{
                    member = associes[index];
                    this.setState({member:associes[index],gerants:gerants})
                }

                if(member.signatureStatut === "" && this.state.codeSMSsended === ""){

                        let msg = "Votre code de vérification de signature est: \n";
                        let codeSMS = (Math.floor(100000 + Math.random() * 900000)).toString();
                        signatureService.sendSMSToActio({
                            msg: msg,
                            url: codeSMS,
                            to:member.phone.replace(/\s+/g, '')
                        }).then(resSMS => {
                            if (resSMS && resSMS.status === 200) {
                                console.log("SMS CODE SENDED");
                                this.setState({
                                    codeSMSsended: codeSMS,
                                });
                            } else {
                                this.openSnackbar('error', "L'envoi du SMS est échoué ! Veuillez contacter l'administrateur");
                            }
                        });
                }
            });
    }

    openSnackbar = (type, msg) => {
        this.setState({
            openAlert: true,
            alertMessage: msg,
            alertType: type
        });
    };

    closeSnackbar = (event, reason) => {
        if (reason === 'clickaway') return;
        this.setState({openAlert: false});
    };


    showStatutDocument = (uid, pays) => event => {

        this.setState({loading: true});

        augmCapitalService.getEntrepriseStatut(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showActeConstitutifDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateActeConstitutif(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showProcurationDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateProcuration(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showDeclarationDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateDeclaration(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showOptingOutGerantDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateOptingOutGerant(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showRequisitionDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateRequisition(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };


    validerSignatureGerant = ()  => {

        let {uid, index} = this.props.match.params;

        let emailsRecivers = [];
        this.state.gerants.map((gerant,key) => {
            emailsRecivers.push({email:gerant.email});
        });
        let fullnameHaveSigneDocs = this.state.member.firstname+" "+this.state.member.lastname;
        let subject = "Création SARL Suisse: "+fullnameHaveSigneDocs + " àsigné les documents";

        let msg = "Cher(e) client(e), " + "<br/><br/><br/>" + fullnameHaveSigneDocs +
            "à signé les documents.<br/>"+"Pour visualiser l'état d'avancement de la procédure, cliquer sur le lien ci-dessous:" + "<br/><br/>";

        let linkUrl = "Accéder ->";
        let url = "http://51.15.229.251:89/creation/SARL/"+ uid +"/details";

        let footerMsg ="<br/><br/>Si vous avez des questions sur le service ou besoin de parler à un spécialiste, contacter-nous directement:<br/><br/>"+
            "Par email : en répondant à cet email !(on vous répond dans la journée)<br/>Par téléphone : au <b>01 76 39 00 60(lundi-vendredi, 8h-20h)</b>"+
            "<br/><br/>"+"A très bientôt,<br/><br/><br/>"+"<b>L'équipe SmartCo.link</b>";

        let signature = this.sigCanvas.getTrimmedCanvas().toDataURL('image/png');

        firebase.database().ref('society/' + uid + '/sAdministrator/' + index).update({
            'signatureStatut': signature
        }).then(data => {
            entrepriseSARLService.sendEmailNotifToGerant({emailsRecivers:JSON.stringify(emailsRecivers),fullnameHaveSigneDocs:fullnameHaveSigneDocs,subject:subject,linkUrl:linkUrl,url:url,msg:msg,footerMsg:footerMsg}).then(res => {
                console.log("EMAIL SENDED");
            },err => {
                console.error("EMAIL NOT SENDED");
            })
            this.openSnackbar('success', "La signature est ajouté avec succès");
            this.setState({codeSMSsended: "", codeSMSTaped: ""})
        }).catch(function (error) {
            alert(error);
        });

    };

    validerSignatureAssoc = ()  => {

        let {uid, index} = this.props.match.params;

        let emailsRecivers = [];
        this.state.gerants.map((gerant,key) => {
            emailsRecivers.push({email:gerant.email});
        });
        let fullnameHaveSigneDocs = this.state.member.firstname+" "+this.state.member.lastname;
        let subject = "Création SARL Suisse: "+fullnameHaveSigneDocs + " à signé les documents";

        let msg = "Cher(e) client(e), " + "<br/><br/><br/>" + fullnameHaveSigneDocs +
            " à signé les documents.<br/>"+"Pour visualiser l'état d'avancement de la procédure, cliquer sur le bouton ci-dessous:" + "<br/><br/>";

        let linkUrl = "Accéder ->";
        let url = "http://51.15.229.251:89/creation/SARL/"+ uid +"/details";

        let footerMsg ="<br/><br/>Si vous avez des questions sur le service ou besoin de parler à un spécialiste, contacter-nous directement:<br/><br/>"+
            "Par email : en répondant à cet email !(on vous répond dans la journée)<br/>Par téléphone : au <b>01 76 39 00 60(lundi-vendredi, 8h-20h)</b>"+
            "<br/><br/>"+"A très bientôt,<br/><br/><br/>"+"<b>L'équipe SmartCo.link</b>";

        let signature = this.sigCanvas.getTrimmedCanvas().toDataURL('image/png');

        firebase.database().ref('society/' + uid + '/sAssociate/' + index).update({
            'signatureStatut': signature
        }).then(data => {
            entrepriseSARLService.sendEmailNotifToGerant({emailsRecivers:JSON.stringify(emailsRecivers),fullnameHaveSigneDocs:fullnameHaveSigneDocs,subject:subject,linkUrl:linkUrl,url:url,msg:msg,footerMsg:footerMsg}).then(res => {
                console.log("EMAIL SENDED");
            },err => {
                console.error("EMAIL NOT SENDED");
            })
            this.openSnackbar('success', "La signature est ajouté avec succès");
            this.setState({codeSMSsended: "", codeSMSTaped: ""})
        }).catch(function (error) {
            alert(error);
        });

    };


    render() {
        const {type, uid, email} = this.props.match.params;

        return (
            <div className="app">
                <header id="topnav">
                    <Suspense fallback={loading()}>
                        <Topbar changeActivePage={this.changeActivePage}/>
                    </Suspense>
                </header>

                <div className="wrapper" style={{paddingTop: 90}}>
                    <Container fluid>
                        <Suspense fallback={loading()}>
                            {this.state.loading && <Loader/>}

                            <div className="row" style={{marginTop: 5}}>
                                <div className="col-lg-12">
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="card-title">
                                            </div>
                                            <div style={{backgroundColor: "#C0C0C0", height: 1}}/>


                                            <div style={{marginTop: 50}}>

                                                <div className="row">
                                                    <div className="col-lg-2"/>
                                                    <div className="col-lg-8">
                                                        <div className="card" style={{boxShadow:"0 0.75rem 6rem rgba(56, 65, 74, 0.15)"}}>
                                                            <div className="card-body">

                                                                <div className="text-center mb-4">
                                                                    <span className="font-18">{email} vous invite à signer
                                                                        éléctroniquement les documents ci dessous:</span>
                                                                </div>
                                                                <div style={{backgroundColor: "#C0C0C0", height: 1}}/>

                                                                {
                                                                    type === "gerant" ?

                                                                        <div>

                                                                            <div style={{marginTop: 25}}>
                                                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showStatutDocument(uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                                </div>
                                                                                <h5 style={{color: "grey"}}>Statut</h5>
                                                                                <div style={{
                                                                                    backgroundColor: "#F0F0F0",
                                                                                    height: 1,
                                                                                    marginTop: 30
                                                                                }}/>
                                                                            </div>

                                                                            <div style={{marginTop: 50}}>
                                                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showOptingOutGerantDocument(uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                                </div>
                                                                                <h5 style={{color: "grey"}}>OuptingOutGérant</h5>
                                                                                <div style={{
                                                                                    backgroundColor: "#F0F0F0",
                                                                                    height: 1,
                                                                                    marginTop: 30
                                                                                }}/>
                                                                            </div>

                                                                            <div style={{marginTop: 50}}>
                                                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showRequisitionDocument(uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                                </div>
                                                                                <h5 style={{color: "grey"}}>Réquisition</h5>
                                                                                <div style={{
                                                                                    backgroundColor: "#F0F0F0",
                                                                                    height: 1,
                                                                                    marginTop: 30
                                                                                }}/>
                                                                            </div>

                                                                        </div> :

                                                                        <div>

                                                                            <div style={{marginTop: 25}}>
                                                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showStatutDocument(uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                                </div>
                                                                                <h5 style={{color: "grey"}}>Statut</h5>
                                                                                <div style={{
                                                                                    backgroundColor: "#F0F0F0",
                                                                                    height: 1,
                                                                                    marginTop: 30
                                                                                }}/>
                                                                            </div>

                                                                            <div style={{marginTop: 50}}>
                                                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showProcurationDocument(uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                                </div>
                                                                                <h5 style={{color: "grey"}}>Procuration</h5>
                                                                                <div style={{
                                                                                    backgroundColor: "#F0F0F0",
                                                                                    height: 1,
                                                                                    marginTop: 30
                                                                                }}/>
                                                                            </div>

                                                                            <div style={{marginTop: 50}}>
                                                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showDeclarationDocument(uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                                </div>
                                                                                <h5 style={{color: "grey"}}>Déclaration I et II</h5>
                                                                                <div style={{
                                                                                    backgroundColor: "#F0F0F0",
                                                                                    height: 1,
                                                                                    marginTop: 30
                                                                                }}/>
                                                                            </div>


                                                                        </div>

                                                                }
                                                               {/* <div style={{marginTop: 50}}>
                                                                    <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showActeConstitutifDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                    </div>
                                                                    <h5 style={{color: "grey"}}>Acte constitutif</h5>
                                                                    <div style={{
                                                                        backgroundColor: "#F0F0F0",
                                                                        height: 1,
                                                                        marginTop: 30
                                                                    }}/>
                                                                </div>*/}

                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="col-lg-2"/>

                                                </div>


                                                {

                                                    this.state.member.signatureStatut === "" ?
                                                        <div className="text-center mt-4 signatureCard">
                                                            <p className="font-17 text-dark">Dessinez votre
                                                                signature avec votre souris ci-dessous</p>
                                                            <div
                                                                className="alert alert-warning fade show text-center pb-0"
                                                                role="alert">
                                                                        <span>Reproduisez votre signature <span
                                                                            className="font-weight-bold">telle qu'elle figure sur votre pièce d'dentité.</span></span>
                                                                <p>(ATTENTION: pas vos initiales, pas votre nom
                                                                    complet)</p>
                                                            </div>
                                                            <input type="text" placeholder="Code reçu par SMS"
                                                                   className="form-control mb-2" value={this.state.codeSMSTaped}
                                                                   onChange={(event) => this.setState({codeSMSTaped: event.target.value})}/>
                                                            <div style={{display: "inline-block"}}>
                                                                <div style={{
                                                                    width: "100%",
                                                                    height: 130,
                                                                    maxWidth: 248,
                                                                    border: '1px solid #C0C0C0'
                                                                }}>
                                                                    <SignatureCanvas ref={(ref) => {
                                                                        this.sigCanvas = ref
                                                                    }} penColor='#000' canvasProps={{
                                                                        width: "250%",
                                                                        height: 128,
                                                                        className: 'sigCanvas',
                                                                        maxWidth: 248
                                                                    }}/>
                                                                </div>
                                                                <button type="button"
                                                                        className="btn btn-light waves-effect mt-2"
                                                                        onClick={() => {
                                                                            this.sigCanvas.clear()
                                                                        }}>Effacer
                                                                </button>
                                                                &nbsp;&nbsp;
                                                                <button type="button"
                                                                        disabled={this.state.codeSMSsended === "" || (this.state.codeSMSTaped !== this.state.codeSMSsended)}
                                                                        className="btn btn-success waves-effect mt-2"
                                                                        onClick={type === "gerant" ? this.validerSignatureGerant : this.validerSignatureAssoc}>
                                                                    Valider
                                                                </button>
                                                            </div>
                                                        </div> :

                                                        <div className="alert alert-success fade show text-center pb-1"
                                                            role="alert">
                                                            <span className="font-weight-bold">Félicitation ! <span>Votre signature est bien ajouté</span></span>
                                                        </div>


                                                }




                                            </div>

                                        </div>
                                    </div>
                                </div>

                            </div>


                            <Snackbar
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'left',
                                }}
                                open={this.state.openAlert}
                                autoHideDuration={5000}
                                onClose={this.closeSnackbar}
                            >
                                <MySnackbarContentWrapper
                                    onClose={this.closeSnackbar}
                                    variant={this.state.alertType}
                                    message={this.state.alertMessage}
                                />
                            </Snackbar>


                        </Suspense>
                    </Container>
                </div>


            </div>


        )
    }

}

export default signDocsSARLFromInvitation;