    saveRespTokenization = (event, values) => {
        this.setState({loadingBtnS1: true});
        firebase.database().ref("society/" + localStorage.getItem("uid") + "Suisse").update({
            'respTokenization': {
                username: values.respTokenUname,
                password: values.respTokenPass,
                email: values.respTokenEmail,
                phone: values.respTokenPhone,
                isAccreditedInvestisor: this.state.isAccreditedInvestisor,
                validated: false
            }
        }).then(ok => {

            let msg = "Votre code de vérification est: \n";
            let codeSMS = (Math.floor(100000 + Math.random() * 900000)).toString();
            signatureService.sendSMSToActio({
                msg: msg,
                url: codeSMS,
                to: values.respTokenPhone.replace(/\s+/g, '')
            }).then(resSMS => {

                if (resSMS && resSMS.status === 200) {
                    console.log("SMS CODE SENDED");
                    this.setState({loadingBtnS1: false, tokenizationStep: "confirmAccount", codeSMSsended: codeSMS,});
                    this.openSnackbar('success', "Félicitation ! Le création du compte est bien validé");
                } else {
                    console.log(resSMS)
                }
            });

        })

    };

