import React, {Component, Suspense} from "react";
import Loader from "../../../components/Loader";
import france from "../../../assets/images/flags/france.png"
import suisse from "../../../assets/images/flags/suisse.png"
import tunisie from "../../../assets/images/flags/tunisie.png"
import {Button, Container} from "reactstrap";
import firebase from "firebase/app";
import "firebase/database"
import augmCapitalService from "../../../provider/augmCapitalService";
import entrepriseSARLService from "../../../provider/entrepriseSARLService";
import pdfImage from "../../../assets/images/pdfimage.jpg";
import SignatureCanvas from "react-signature-canvas";
import MySnackbarContentWrapper from "../../../tools/customSnackBar";
import Snackbar from "@material-ui/core/Snackbar";
import signatureService from "../../../provider/signatureService";


const Topbar = React.lazy(() => import("../../../components/Topbar"));
const Navbar = React.lazy(() => import("../../../components/Navbar"));
const loading = () => <Loader/>;

class SARLAfterCreationPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            openAlert: false,
            alertMessage: '',
            alertType: '',
            loading: false,

            societe:'',

            gerants: [],
            associes: [],

            codeSMSTaped:'',
            codeSMSsended:'',

            nbSignaturesMissed:''
        };
    }

    sigCanvas = {};

    componentWillMount() {
        const {uid} = this.props.match.params;
        console.log(uid);

        let userUid = uid.replace('Suisse','');
        this.setState({userUid:userUid});

        firebase.database().ref('/society/' + uid).on('value', (snapshot) => {
            let societie = snapshot.val() || [];
            let gerants = societie.sAdministrator || [];
            let showFormSignatureGerArray = [];
            let showFormSignatureAssocArray = [];
            let nbNotSigned = 0;
            gerants.map((item, key) => {
                showFormSignatureGerArray.push({show: false});
                item.signatureStatut === "" && nbNotSigned++;
            });
            let associes = societie.sAssociate || [];
            associes.map((item, key) => {
                showFormSignatureAssocArray.push({show: false})
                item.signatureStatut === "" && nbNotSigned++;
            });

            firebase.database().ref('/users/'+userUid).on('value',(snapshot) => {
                let user = snapshot.val();
                this.setState({
                    societe:societie,
                    uid: uid,
                    gerants: gerants,
                    showFormGerSignatureArray: showFormSignatureGerArray,
                    showFormAssocSignatureArray: showFormSignatureAssocArray,
                    associes: associes,
                    nbSignaturesMissed:nbNotSigned,
                    userGId:user.idGeneve,
                    userGFirstname:user.firstnameG,
                    userGLastname:user.lastnameG
                })
            });



        });


    }

    handleArrayObjectChange = (array, name, key, value) => event => {

        if(value === true && this.state.codeSMSsended === "") {
            let msg = "Votre code de vérification de signature est: \n";
            let codeSMS = (Math.floor(100000 + Math.random() * 900000)).toString();
            signatureService.sendSMSToActio({
                msg: msg,
                url: codeSMS,
                to: array === "showFormGerSignatureArray" ? this.state.gerants[key].phone.replace(/\s+/g, '') : this.state.associes[key].phone.replace(/\s+/g, '')
            }).then(resSMS => {
                if (resSMS && resSMS.status === 200) {
                    console.log("SMS CODE SENDED");
                    this.setState({
                        codeSMSsended: codeSMS,
                    });
                } else {
                    console.log(resSMS)
                }
            });
        }

        let copy = this.state[array];
        copy[key][name] = value;
        this.setState({
            [array]: copy
        })
    };

    openSnackbar = (type, msg) => {
        this.setState({
            openAlert: true,
            alertMessage: msg,
            alertType: type
        });
    };

    closeSnackbar = (event, reason) => {
        if (reason === 'clickaway') return;
        this.setState({openAlert: false});
    };


    showStatutDocument = (uid, pays) => event => {

        this.setState({loading: true});

        augmCapitalService.getEntrepriseStatut(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showActeConstitutifDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateActeConstitutif(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showProcurationDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateProcuration(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});


        }, err => {
            console.log(err);
        })
    };

    showDeclarationDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateDeclaration(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showOptingOutGerantDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateOptingOutGerant(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    showRequisitionDocument = (uid, pays) => event => {

        this.setState({loading: true});

        entrepriseSARLService.GenerateRequisition(uid, pays).then(res => {

            const file = new Blob(
                [res],
                {type: 'application/pdf'});
            const fileURL = URL.createObjectURL(file);
            window.open(fileURL);
            this.setState({loading: false});

        }, err => {
            console.log(err);
        })
    };

    validerSignatureGerant = (index) => event => {

        let signature = this.sigCanvas.getTrimmedCanvas().toDataURL('image/png');

        firebase.database().ref('society/' + this.state.uid + '/sAdministrator/' + index).update({
            'signatureStatut': signature
        }).then(data => {
            this.openSnackbar('success', "La signature est ajouté avec succès");
            this.setState({codeSMSsended:"",codeSMSTaped:""})
        }).catch(function (error) {
            alert(error);
        });

    };

    validerSignatureAssoc = (index) => event => {

        let signature = this.sigCanvas.getTrimmedCanvas().toDataURL('image/png');

        firebase.database().ref('society/' + this.state.uid + '/sAssociate/' + index).update({
            'signatureStatut': signature
        }).then(data => {
            this.openSnackbar('success', "La signature est ajouté avec succès");
        }).catch(function (error) {
            alert(error);
        });

    };

    sendInvitation =(email,index, type) => event => {

        let subject = "Demande de signature sur SmartCo.link";
        let msg = "Bonjour, " + "<br/><br/><br/>" +
            "Vous etes invité par <b>"+this.state.gerants[0].email+"</b> à signer des documents éléctroniquement." + "<br/><br/>" +
            "Pour signer vos documents, veuillez cliquer sur le lien ci-dessous:"+"<br/><br/>";

        let linkUrl = "Signer ->";
        let url = "http://51.15.229.251:88/signature/SARL/"+this.state.uid+"/"+type+"/"+index+"/by/"+this.state.gerants[0].email;

        let footerMsg ="<br/><br/>"+"A très bientôt,<br/><br/><br/>"+"<b>L'équipe SmartCo.link</b>";

        entrepriseSARLService.sendInvitation({emailReciver:email,subject:subject,linkUrl:linkUrl,url:url,msg:msg,footerMsg:footerMsg}).then(result => {
            console.log("EMAIL SENDED");
            this.openSnackbar('success', "Un mail de demande de signature est bien envoyé à "+email);
        },err => {
            console.log(err);
            this.openSnackbar('error', "Une erreur est parvenue lors de l'envoi du demande de signature au ! Veuillez réessayer une autre fois ");
        })

    };


    signDocWithGeneve = (name) => event => {

        name === "statut" ? this.setState({loadingBtnStatut:true}) :
            name === "procuration" ? this.setState({loadingBtnProc:true}) :
                name === "declaration" ? this.setState({loadingBtnDec:true}) :
                    name === "ouptingOutGerant" ? this.setState({loadingBtnOpt:true}) :
                        name === "requisition" ? this.setState({loadingBtnReq:true}) : this.setState({loadingBtnActe:true}) ;


        entrepriseSARLService.getDocSARLDocDigest(name,this.state.uid,"Suisse").then(res => {
            console.log(res);

            entrepriseSARLService.loginGeneve().then(auth => {
                console.log(auth.data.jwt);
                entrepriseSARLService.signGeneveDoc(name+"-SARL",res.digest,auth.data.jwt,this.state.userGId,this.state.userGFirstname,this.state.userGLastname).then( r => {
                    console.log(r);
                    if(r.succes === true){

                        if(r.data.SignatureResponse.ipfsHash !== null && r.data.SignatureResponse.ipfsHash !== undefined ){

                            if(name === "statut"){
                                firebase.database().ref('society/' + this.state.uid).update({
                                    'ipfsHashStatut': r.data.SignatureResponse.ipfsHash
                                }).then(data => {
                                    this.setState({loadingBtnStatut:false});
                                    this.openSnackbar('success', "La signature est bien acceptée");
                                }).catch(function (error) {alert(error);});
                            }
                            if(name === "procuration"){
                                firebase.database().ref('society/' + this.state.uid).update({
                                    'ipfsHashProc': r.data.SignatureResponse.ipfsHash
                                }).then(data => {
                                    this.setState({loadingBtnProc:false});
                                    this.openSnackbar('success', "La signature est bien acceptée");
                                }).catch(function (error) {alert(error);});
                            }
                            if(name === "declaration"){
                                firebase.database().ref('society/' + this.state.uid).update({
                                    'ipfsHashDec': r.data.SignatureResponse.ipfsHash
                                }).then(data => {
                                    this.setState({loadingBtnDec:false});
                                    this.openSnackbar('success', "La signature est bien acceptée");
                                }).catch(function (error) {alert(error);});
                            }
                            if(name === "ouptingOutGerant"){
                                firebase.database().ref('society/' + this.state.uid).update({
                                    'ipfsHashOpt': r.data.SignatureResponse.ipfsHash
                                }).then(data => {
                                    this.setState({loadingBtnOpt:false})
                                    this.openSnackbar('success', "La signature est bien acceptée");
                                }).catch(function (error) {alert(error);});
                            }
                            if(name === "requisition"){
                                firebase.database().ref('society/' + this.state.uid).update({
                                    'ipfsHashReq': r.data.SignatureResponse.ipfsHash
                                }).then(data => {
                                    this.setState({loadingBtnReq:false})
                                    this.openSnackbar('success', "La signature est bien acceptée");
                                }).catch(function (error) {alert(error);});
                            }
                            if(name === "acte"){
                                firebase.database().ref('society/' + this.state.uid).update({
                                    'ipfsHashActe': r.data.SignatureResponse.ipfsHash
                                }).then(data => {
                                    this.setState({loadingBtnActe:false})
                                    this.openSnackbar('success', "La signature est bien acceptée");
                                }).catch(function (error) {alert(error);});
                            }



                        }else{
                            this.setState({loadingBtnStatut:false,loadingBtnProc:false,loadingBtnDec:false,loadingBtnOpt:false,loadingBtnReq:false,loadingBtnActe:false})
                            this.openSnackbar('error', "Signature non acceptée");

                        }
                    }else{
                        this.setState({loadingBtnStatut:false,loadingBtnProc:false,loadingBtnDec:false,loadingBtnOpt:false,loadingBtnReq:false,loadingBtnActe:false})
                        this.openSnackbar('error', "Une erreur est survenue !");
                    }


                }).catch( err => {
                    this.setState({loadingBtnStatut:false,loadingBtnProc:false,loadingBtnDec:false,loadingBtnOpt:false,loadingBtnReq:false,loadingBtnActe:false})
                    this.openSnackbar('error', "Une erreur est survenue !");
                    console.log(err);
                })


            }).catch(err => {

                this.setState({loadingBtnStatut:false,loadingBtnProc:false,loadingBtnDec:false,loadingBtnOpt:false,loadingBtnReq:false,loadingBtnActe:false})
                this.openSnackbar('error', "Une erreur est survenue !");
                console.log(err);


            });



        }).catch(err => {
            this.setState({loadingBtnStatut:false,loadingBtnProc:false,loadingBtnDec:false,loadingBtnOpt:false,loadingBtnReq:false,loadingBtnActe:false})
            this.openSnackbar('error', "Une erreur est survenue !");
            console.log(err);
        })

    };


    render() {
        return (
            <div className="app">
                <header id="topnav">
                    <Suspense fallback={loading()}>
                        <Topbar changeActivePage={this.changeActivePage}/>
                    </Suspense>
                </header>

                <div className="wrapper" style={{paddingTop: 90}}>
                    <Container fluid>
                        <Suspense fallback={loading()}>
                            {this.state.loading && <Loader/>}

                            {
                                this.state.nbSignaturesMissed === 0 ?
                                    <div className="alert alert-success alert-dismissible fade show text-center pb-1"
                                         role="alert">
                                        <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                                            <span aria-hidden="true">&times;</span>
                                        </button>
                                        <span>Félicitation ! Tous les documents sont signés !</span>
                                    </div> :

                                    <div className="alert alert-warning alert-dismissible fade show text-center pb-0"
                                         role="alert">
                                        <button type="button" className="close" data-dismiss="alert" aria-label="Close">
                                            <span aria-hidden="true">&times;</span>
                                        </button>

                                        <span className="font-17">Félicitation ! Vos documents sont prêts à être signés !</span>
                                        <p>Il ne manque que {this.state.nbSignaturesMissed} signatures pour finaliser votre
                                            dossier </p>


                                    </div>
                            }



                            <div className="row" style={{marginTop: 5}}>
                                <div className="col-lg-8">
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="card-title">
                                                <h4>Signature des documents</h4>
                                            </div>
                                            <div style={{backgroundColor: "#C0C0C0", height: 1}}/>

                                            {
                                                this.state.gerants.map((gerant, i) => (
                                                    gerant.fullname = gerant.firstname + " " + gerant.lastname,
                                                        <div style={{marginTop: 50}}>
                                                            <div className="card-widgets">
                                                                {
                                                                    gerant.signatureStatut === "" ?
                                                                        <span
                                                                            className="badge bg-soft-danger text-danger p-1 text-uppercase">Signature requise</span> :
                                                                        <span
                                                                            className="badge bg-soft-success text-success p-1 text-uppercase">Signé</span>
                                                                }

                                                            </div>
                                                            <h5 style={{color: "grey"}}>{gerant.fullname}(Président,Fondateur)</h5>
                                                            {
                                                                gerant.signatureStatut === "" ?
                                                                    <div>
                                                                        <button type="button" className="btn btn-pink waves-effect mt-2"
                                                                                onClick={this.handleArrayObjectChange('showFormGerSignatureArray','show',i,true)}>
                                                                            Signer
                                                                        </button>
                                                                        &nbsp;&nbsp;
                                                                        <button type="button" onClick={this.sendInvitation(gerant.email,i,"gerant")}
                                                                                className="btn btn-outline-info waves-effect mt-2">Demande de signature
                                                                        </button>
                                                                    </div> :
                                                                    <button type="button"
                                                                            className="btn btn-outline-info waves-effect mt-2">Modifier
                                                                    </button>

                                                            }

                                                            {
                                                                this.state.showFormGerSignatureArray[i].show &&
                                                                <div>
                                                                    <div className="text-center mt-3">
                                                                        <p className="font-17 text-dark">Signer éléctroniquement des documents ci-dessous:</p>
                                                                    </div>
                                                                    <div className="mt-4 signatureCard">
                                                                        <div style={{marginTop: 25}}>
                                                                            <div className="card-widgets">
                                                                               <span className="badge bg-soft-light text-dark p-1 pointer"
                                                                                     onClick={this.showStatutDocument(this.state.uid, "Suisse")}>
                                                                                     <i style={{color: 'green', fontSize: 13}} className="fe-zoom-in"></i>&nbsp;Prévisualiser&nbsp;&nbsp;
                                                                                   <img src={pdfImage} style={{maxHeight: 25, objectFit: "contain"}} alt="attachment"
                                                                                        className="img-thumbnail img-responsive"/>
                                                                               </span>
                                                                            </div>
                                                                            <h5 style={{color: "grey"}}>Statut</h5>
                                                                            <div style={{backgroundColor: "#F0F0F0", height: 1, marginTop: 30}}/>
                                                                        </div>
                                                                        <div style={{marginTop: 50}}>
                                                                            <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showOptingOutGerantDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                            </div>
                                                                            <h5 style={{color: "grey"}}>OuptingOutGérant</h5>
                                                                            <div style={{
                                                                                backgroundColor: "#F0F0F0",
                                                                                height: 1,
                                                                                marginTop: 30
                                                                            }}/>
                                                                        </div>

                                                                        <div style={{marginTop: 50}}>
                                                                            <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showRequisitionDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                            </div>
                                                                            <h5 style={{color: "grey"}}>Réquisition</h5>
                                                                            <div style={{
                                                                                backgroundColor: "#F0F0F0",
                                                                                height: 1,
                                                                                marginTop: 30
                                                                            }}/>
                                                                        </div>
                                                                    </div>

                                                                    <div className="text-center mt-3 signatureCard">
                                                                        <div className="card-widgets">
                                                                            <i style={{color: 'green', fontSize: 16,cursor:"pointer"}}
                                                                               className="mdi mdi-close" onClick={this.handleArrayObjectChange('showFormGerSignatureArray','show',i,false)}></i>
                                                                        </div>
                                                                        <p className="font-17 text-dark">Dessinez votre
                                                                            signature avec votre souris ci-dessous</p>
                                                                        <div
                                                                            className="alert alert-warning fade show text-center pb-0"
                                                                            role="alert">
                                                                        <span>Reproduisez votre signature <span
                                                                            className="font-weight-bold">telle qu'elle figure sur votre pièce d'dentité.</span></span>
                                                                            <p>(ATTENTION: pas vos initiales, pas votre nom
                                                                                complet)</p>
                                                                        </div>
                                                                        <input type="text" placeholder="Code reçu par SMS" className="form-control mb-2" value={this.state.codeSMSTaped}
                                                                               onChange={(event) => this.setState({codeSMSTaped:event.target.value})} />
                                                                        <div style={{display: "inline-block"}}>
                                                                            <div style={{
                                                                                width: "100%",
                                                                                height: 130,
                                                                                maxWidth: 248,
                                                                                border: '1px solid #C0C0C0'
                                                                            }}>
                                                                                <SignatureCanvas ref={(ref) => {
                                                                                    this.sigCanvas = ref
                                                                                }} penColor='#000' canvasProps={{
                                                                                    width: "250%",
                                                                                    height: 128,
                                                                                    className: 'sigCanvas',
                                                                                    maxWidth: 248
                                                                                }}/>
                                                                            </div>
                                                                            <button type="button"
                                                                                    className="btn btn-light waves-effect mt-2"
                                                                                    onClick={() => {
                                                                                        this.sigCanvas.clear()
                                                                                    }}>Effacer
                                                                            </button>
                                                                            &nbsp;&nbsp;
                                                                            <button type="button"
                                                                                    disabled={this.state.codeSMSsended === "" || (this.state.codeSMSTaped !== this.state.codeSMSsended)}
                                                                                    className="btn btn-success waves-effect mt-2"
                                                                                    onClick={this.validerSignatureGerant(i)}>
                                                                                Valider
                                                                            </button>
                                                                        </div>
                                                                    </div>
                                                                </div>

                                                            }


                                                            <div style={{
                                                                backgroundColor: "#F0F0F0",
                                                                height: 1,
                                                                marginTop: 30
                                                            }}/>
                                                        </div>
                                                ))
                                            }
                                            {
                                                this.state.associes.map((associe, i) => (
                                                    associe.fullname = associe.firstname !== "" ? associe.firstname + " " + associe.lastname : associe.ej_name,
                                                        <div style={{marginTop: 50}}>
                                                            <div className="card-widgets">
                                                                {
                                                                    associe.signatureStatut === "" ?
                                                                        <span
                                                                            className="badge bg-soft-danger text-danger p-1 text-uppercase">Signature requise</span> :
                                                                        <span
                                                                            className="badge bg-soft-success text-success p-1 text-uppercase">Signé</span>
                                                                }
                                                            </div>
                                                            <h5 style={{color: "grey"}}>{associe.fullname}(Associé)</h5>
                                                            {
                                                                associe.signatureStatut === "" ?
                                                                    <div>
                                                                        <button type="button" className="btn btn-pink waves-effect mt-2"
                                                                                disabled={this.state.gerants[0].signatureStatut === "" || ( i !== 0 && this.state.associes[i-1].signatureStatut === "")}
                                                                                onClick={this.handleArrayObjectChange('showFormAssocSignatureArray','show',i,true)}>Signer
                                                                        </button>
                                                                        &nbsp;&nbsp;
                                                                        <button type="button" onClick={this.sendInvitation(associe.email,i,"associe")}
                                                                                className="btn btn-outline-info waves-effect mt-2">Demande de signature
                                                                        </button>
                                                                    </div> :

                                                                    <button type="button"
                                                                            className="btn btn-outline-info waves-effect mt-2">Modifier
                                                                    </button>

                                                            }


                                                            {
                                                                this.state.showFormAssocSignatureArray[i].show &&

                                                                <div>
                                                                    <div className="text-center mt-3">
                                                                        <p className="font-17 text-dark">Signer éléctroniquement des documents ci-dessous:</p>
                                                                    </div>
                                                                    <div className="mt-4 signatureCard">
                                                                        <div style={{marginTop: 25}}>
                                                                            <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showStatutDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                            </div>
                                                                            <h5 style={{color: "grey"}}>Statut</h5>
                                                                            <div style={{
                                                                                backgroundColor: "#F0F0F0",
                                                                                height: 1,
                                                                                marginTop: 30
                                                                            }}/>
                                                                        </div>

                                                                        <div style={{marginTop: 50}}>
                                                                            <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showProcurationDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                            </div>
                                                                            <h5 style={{color: "grey"}}>Procuration</h5>
                                                                            <div style={{
                                                                                backgroundColor: "#F0F0F0",
                                                                                height: 1,
                                                                                marginTop: 30
                                                                            }}/>
                                                                        </div>

                                                                        <div style={{marginTop: 50}}>
                                                                            <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer"
                                                          onClick={this.showDeclarationDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                                            </div>
                                                                            <h5 style={{color: "grey"}}>Déclaration I et II</h5>
                                                                            <div style={{
                                                                                backgroundColor: "#F0F0F0",
                                                                                height: 1,
                                                                                marginTop: 30
                                                                            }}/>
                                                                        </div>
                                                                    </div>

                                                                    <div className="text-center mt-4 signatureCard">
                                                                        <div className="card-widgets">
                                                                            <i style={{color: 'green', fontSize: 16,cursor:"pointer"}}
                                                                               className="mdi mdi-close" onClick={this.handleArrayObjectChange('showFormAssocSignatureArray','show',i,false)}></i>
                                                                        </div>
                                                                        <p className="font-16 text-dark">Dessinez votre
                                                                            signature avec votre souris ci-dessous</p>
                                                                        <div
                                                                            className="alert alert-warning fade show text-center pb-0"
                                                                            role="alert">
                                                                        <span>Reproduisez votre signature <span
                                                                            className="font-weight-bold">telle qu'elle figure sur votre pièce d'dentité.</span></span>
                                                                            <p>(ATTENTION: pas vos initiales, pas votre nom
                                                                                complet)</p>
                                                                        </div>
                                                                        <input type="text" placeholder="Code reçu par SMS" className="form-control mb-2" value={this.state.codeSMSTaped}
                                                                               onChange={(event) => this.setState({codeSMSTaped:event.target.value})} />
                                                                        <div style={{display: "inline-block"}}>
                                                                            <div style={{
                                                                                width: "100%",
                                                                                height: 130,
                                                                                maxWidth: 248,
                                                                                border: '1px solid #C0C0C0'
                                                                            }}>
                                                                                <SignatureCanvas ref={(ref) => {
                                                                                    this.sigCanvas = ref
                                                                                }} penColor='#000' canvasProps={{
                                                                                    width: "250%",
                                                                                    height: 128,
                                                                                    className: 'sigCanvas',
                                                                                    maxWidth: 248
                                                                                }}/>
                                                                            </div>
                                                                            <button type="button"
                                                                                    className="btn btn-light waves-effect mt-2"
                                                                                    onClick={() => {
                                                                                        this.sigCanvas.clear()
                                                                                    }}>Effacer
                                                                            </button>
                                                                            &nbsp;&nbsp;
                                                                            <button type="button"
                                                                                    disabled={this.state.codeSMSsended === "" || (this.state.codeSMSTaped !== this.state.codeSMSsended)}
                                                                                    className="btn btn-success waves-effect mt-2"
                                                                                    onClick={this.validerSignatureAssoc(i)}>
                                                                                Valider
                                                                            </button>
                                                                        </div>
                                                                    </div>
                                                                </div>


                                                            }

                                                            <div style={{
                                                                backgroundColor: "#F0F0F0",
                                                                height: 1,
                                                                marginTop: 30
                                                            }}/>
                                                        </div>
                                                ))
                                            }

                                        </div>
                                    </div>
                                </div>


                                <div className="col-lg-4">
                                    <div className="card">
                                        <div className="card-body">
                                            <div className="card-title">
                                                <h4>Visualiser mes documents</h4>
                                            </div>
                                            <div style={{backgroundColor: "#C0C0C0", height: 1}}/>

                                            <div style={{marginTop: 50}}>
                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer" style={{marginTop:-25}}
                                                          onClick={this.showStatutDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                </div>
                                                <h5 style={{color: "grey"}}>Statut</h5>
                                                {
                                                    this.state.societe.ipfsHashStatut ?
                                                        <span className="font-weight-bold text-dark">ipfsHash :&nbsp;<span className="text-blue">{this.state.societe.ipfsHashStatut} </span></span> :
                                                        <div className="text-center">
                                                            <button type="button" className="btn btn-success btn-micro waves-effect mt-2"
                                                                    onClick={this.signDocWithGeneve("statut")}>
                                                                {
                                                                    this.state.loadingBtnStatut ?
                                                                        <div className="spinner-border avatar-xs text-white m-1" role="status"/>
                                                                        : 'Signer le document'
                                                                }
                                                            </button>
                                                        </div>
                                                }


                                                <div style={{backgroundColor: "#F0F0F0", height: 1, marginTop: 30}}/>
                                            </div>

                                            <div style={{marginTop: 50}}>
                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer" style={{marginTop:-25}}
                                                          onClick={this.showProcurationDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                </div>
                                                <h5 style={{color: "grey"}}>Procuration</h5>
                                                {
                                                    this.state.societe.ipfsHashProc ?
                                                        <span className="font-weight-bold text-dark">ipfsHash :&nbsp;<span className="text-blue">{this.state.societe.ipfsHashProc} </span></span> :
                                                        <div className="text-center">
                                                            <button type="button" className="btn btn-success btn-micro waves-effect mt-2"
                                                                    onClick={this.signDocWithGeneve("procuration")}>
                                                                {
                                                                    this.state.loadingBtnProc ?
                                                                        <div className="spinner-border avatar-xs text-white m-1" role="status"/>
                                                                        : 'Signer le document'
                                                                }
                                                            </button>
                                                        </div>
                                                }
                                                <div style={{backgroundColor: "#F0F0F0", height: 1, marginTop: 30}}/>
                                            </div>

                                            <div style={{marginTop: 50}}>
                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer" style={{marginTop:-25}}
                                                          onClick={this.showDeclarationDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                </div>
                                                <h5 style={{color: "grey"}}>Déclaration I et II</h5>
                                                {
                                                    this.state.societe.ipfsHashDec ?
                                                        <span className="font-weight-bold text-dark">ipfsHash :&nbsp;<span className="text-blue">{this.state.societe.ipfsHashDec} </span></span> :
                                                        <div className="text-center">
                                                            <button type="button" className="btn btn-success btn-micro waves-effect mt-2"
                                                                    onClick={this.signDocWithGeneve("declaration")}>
                                                                {
                                                                    this.state.loadingBtnDec ?
                                                                        <div className="spinner-border avatar-xs text-white m-1" role="status"/>
                                                                        : 'Signer le document'
                                                                }
                                                            </button>
                                                        </div>
                                                }
                                                <div style={{backgroundColor: "#F0F0F0", height: 1, marginTop: 30}}/>
                                            </div>

                                            <div style={{marginTop: 50}}>
                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer" style={{marginTop:-25}}
                                                          onClick={this.showOptingOutGerantDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                </div>
                                                <h5 style={{color: "grey"}}>OuptingOutGérant</h5>
                                                {
                                                    this.state.societe.ipfsHashOpt ?
                                                        <span className="font-weight-bold text-dark">ipfsHash :&nbsp;<span className="text-blue">{this.state.societe.ipfsHashOpt} </span></span> :
                                                        <div className="text-center">
                                                            <button type="button" className="btn btn-success btn-micro waves-effect mt-2"
                                                                    onClick={this.signDocWithGeneve("ouptingOutGerant")}>
                                                                {
                                                                    this.state.loadingBtnOpt ?
                                                                        <div className="spinner-border avatar-xs text-white m-1" role="status"/>
                                                                        : 'Signer le document'
                                                                }
                                                            </button>
                                                        </div>
                                                }
                                                <div style={{backgroundColor: "#F0F0F0", height: 1, marginTop: 30}}/>
                                            </div>

                                            <div style={{marginTop: 50}}>
                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer" style={{marginTop:-25}}
                                                          onClick={this.showRequisitionDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                </div>
                                                <h5 style={{color: "grey"}}>Réquisition</h5>
                                                {
                                                    this.state.societe.ipfsHashReq ?
                                                        <span className="font-weight-bold text-dark">ipfsHash :&nbsp;<span className="text-blue">{this.state.societe.ipfsHashReq} </span></span> :
                                                        <div className="text-center">
                                                            <button type="button" className="btn btn-success btn-micro waves-effect mt-2"
                                                                    onClick={this.signDocWithGeneve("requisition")}>
                                                                {
                                                                    this.state.loadingBtnReq ?
                                                                        <div className="spinner-border avatar-xs text-white m-1" role="status"/>
                                                                        : 'Signer le document'
                                                                }
                                                            </button>
                                                        </div>
                                                }
                                                <div style={{backgroundColor: "#F0F0F0", height: 1, marginTop: 30}}/>
                                            </div>

                                            <div style={{marginTop: 50}}>
                                                <div className="card-widgets">
                                                    <span className="badge bg-soft-light text-dark p-1 pointer" style={{marginTop:-25}}
                                                          onClick={this.showActeConstitutifDocument(this.state.uid, "Suisse")}>
                                                        <i style={{color: 'green', fontSize: 13}}
                                                           className="fe-zoom-in"></i>&nbsp;
                                                        Prévisualiser&nbsp;&nbsp;
                                                        <img src={pdfImage}
                                                             style={{maxHeight: 25, objectFit: "contain"}}
                                                             alt="attachment"
                                                             className="img-thumbnail img-responsive"/>
                                                    </span>
                                                </div>
                                                <h5 style={{color: "grey"}}>Acte constitutif</h5>
                                                {
                                                    this.state.societe.ipfsHashActe ?
                                                        <span className="font-weight-bold text-dark">ipfsHash :&nbsp;<span className="text-blue">{this.state.societe.ipfsHashActe} </span></span> :
                                                        <div className="text-center">
                                                            <button type="button" className="btn btn-success btn-micro waves-effect mt-2"
                                                                    onClick={this.signDocWithGeneve("acte")}>
                                                                {
                                                                    this.state.loadingBtnActe ?
                                                                        <div className="spinner-border avatar-xs text-white m-1" role="status"/>
                                                                        : 'Signer le document'
                                                                }
                                                            </button>
                                                        </div>
                                                }
                                                <div style={{backgroundColor: "#F0F0F0", height: 1, marginTop: 30}}/>
                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </div>


                            <Snackbar
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'left',
                                }}
                                open={this.state.openAlert}
                                autoHideDuration={5000}
                                onClose={this.closeSnackbar}
                            >
                                <MySnackbarContentWrapper
                                    onClose={this.closeSnackbar}
                                    variant={this.state.alertType}
                                    message={this.state.alertMessage}
                                />
                            </Snackbar>


                        </Suspense>
                    </Container>
                </div>


            </div>


        )
    }

}

export default SARLAfterCreationPage;